.products{
	background: #f5f5f5;

	&__descr{
		font-size: 16px;

		p{
			line-height: 1.8;
		}
		h3{
			&:before{
				content: "";
				display: block;
				width: 50px;
				height: 3px;
				background: $red;
				margin-bottom: 25px;
			}
		}
	}
	&__row{
		+ div{
			margin-top: 75px;
		}
	}
}